import {ARTICLE_INFO, ARTICLES_RECEIVED, ARTICLE_FILTER} from "../actions/types";

const INITIAL_STATE = {
    article_info: null,
    articles: [],
    filter:"",
};

export default (state = INITIAL_STATE, action) => {
    switch(action.type){
        case ARTICLE_INFO:
            return  {...state, article_info: action.payload};
        case ARTICLES_RECEIVED:
            return {...state, articles: action.articles};
        case ARTICLE_FILTER:
            return {...state, filter: action.payload};
        default:
            return state;
    }
};
