import {ARTICLES_RECEIVED, ARTICLE_INFO, ARTICLE_FILTER} from "../actions/types";
const { stringify } = require('querystring');

const endpoint_remote = "https://aqueous-coast-74759.herokuapp.com/";
const endpoint_local = "http://192.168.1.3:8080/";

function receiveArticles(_articles){
  return {
    type: ARTICLES_RECEIVED,
    articles: _articles
  }
}

function setArticleInfo(article){
    return {
        type: ARTICLE_INFO,
        payload: article
    }
}

export function gotoArticle(article){

    return (dispatch) => {
        dispatch(setArticleInfo(article));
    }
}

export function setArticleFilter(filter){

    return (dispatch) => {
        dispatch({
            type: ARTICLE_FILTER,
            payload: filter
        })
    }
}

