export function deleteArticle(id, callback = null){
    let config = {
        method: 'DELETE',
        headers: { 'Content-Type':'application/x-www-form-urlencoded','access_token':localStorage.getItem('access_token')},
    }
    return () => {
        return fetch(endpoint_remote + 'deleteArticle/' + id, config)
            .then(response =>
            response.json()
                .then(message =>({message,response}))
            ).then(({message, response}) => {

                    if(response.status != 200){
                        console.log("Failed to delete article");
                        return Promise.reject(message);
                    } else {
                        console.log(message);
                        if(callback!=null){
                            callback();
                        }
                    }
            }).catch(err => console.log(err))
    }
}

export function getArticle(id, callback = null){
    let config = {
        method: 'GET',
        headers: { 'Content-Type':'application/x-www-form-urlencoded'},
    }
    return (dispatch) => {
        return fetch(endpoint_remote + 'getArticle/' + id, config)
          .then(response =>
            response.json()
                .then(article => ({ article, response }))
                ).then(({ article, response }) =>  {

                // TODO Fall into response not ok, all is ok atm
            if (response.status !== 200) {
              // If there was a problem, we want to
              // dispatch the error condition
                console.log("Failed to get article "+id);

              //dispatch(loginError(user.message));
              return Promise.reject(article)
            } else {
              // If login was successful, set the token in local storage
              //localStorage.setItem('articles', article); // TODO remember to look after these on app entry
              // Dispatch the success action

                dispatch(setArticleInfo(article))

             // dispatch(receiveLogin(user));
            }
      }).catch(err => console.log(err))
    }
}

